import { useEffect, useRef, useState } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { TrackballControls } from "three/examples/jsm/controls/TrackballControls";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";

const RotatingCube = () => {
  const mountRef = useRef(null);
  const [added, setAdded] = useState(false); 

  useEffect(() => {
    if (!mountRef.current) return;

    // Scene, Camera, Renderer
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, 586 / 586, 0.1, 1000);
    camera.position.z = 100;

    const renderer = new THREE.WebGLRenderer({ alpha: true });
    renderer.setSize(586, 586);
    if (!added && mountRef.current.children.length == 0){
      console.log('Added');
      mountRef.current.appendChild(renderer.domElement);
      setAdded(true);
    }

    // Load 3D Model
    const loader = new GLTFLoader();
    let model;
    loader.load("/models/cube/scene.gltf", (gltf) => {
      model = gltf.scene;
      model.scale.set(500, 500, 500);
      scene.add(model);
    });

    // Lighting
    const topLight = new THREE.DirectionalLight(0xffffff, 1.5);
    topLight.position.set(500, 500, 500);
    scene.add(topLight);

    const ambientLight = new THREE.AmbientLight(0x333333, 1.5);
    scene.add(ambientLight);

    // Controls
    const trackballControls = new TrackballControls(camera, renderer.domElement);
    trackballControls.minDistance = 80;
    trackballControls.maxDistance = 100;
    trackballControls.staticMoving = true;

    const orbitControls = new OrbitControls(camera, renderer.domElement);
    orbitControls.enableZoom = false;
    orbitControls.enableRotate = false;
    orbitControls.autoRotate = true;

    // Animate Function
    const animate = () => {
      requestAnimationFrame(animate);
      trackballControls.update();
      orbitControls.update();
      renderer.render(scene, camera);
    };
    animate();

    // Handle Window Resize
    const handleResize = () => {
      camera.aspect = 586 / 586;
      camera.updateProjectionMatrix();
      renderer.setSize(586, 586);
    };
    window.addEventListener("resize", handleResize);

    // Cleanup Function
    return () => {
      window.removeEventListener("resize", handleResize);
      if (mountRef.current) {
        mountRef.current.removeChild(renderer.domElement); // Prevents the error
      }
      renderer.dispose(); // Clean up Three.js resources
    };
  }, []);

  return <div ref={mountRef} style={{ width: '100%', height: '100%'}}/>;
};

export default RotatingCube;
