import React from "react";
import RotatingCube from '../../../components/RotatingCube/RotatingCube';

export default function Land(){
  return (
    <div className="land">
      {/* Left */}
      <div className="m0 flex column land-typo" >

        {/* Typo */}
        <div className="box m0" style={{ flexGrow: 1, minHeight: '60%'}}>
          Coming soon ...
        </div>

        {/* Marquee */}
        <div className="box m0 mt media nomobile" style={{ flexGrow: 1, flexShrink: 1}}>
        </div>
      </div>

      {/* Right */}
      <div className="box m0 land-display" style={{ display: 'flex', justifyContent: 'stretch', alignItems: 'stretch'}}>
        <RotatingCube/>
      </div>
    </div>
  );
}